





























































export default {
	data() {
		return {
			autoplay: true,
			swiperList: [],
			cardList:[],
		}
	},
	onLoad(e) {
		if(e.shareId){
			uni.setStorageSync('shareId', e.shareId);
		}
		
		if(e.agentId){
			uni.setStorageSync('agentId', e.agentId);
		}
			
		if(e.qdId){
			uni.setStorageSync('qdId', e.qdId);
		}
	    //uni.showLoading();
		//uni.showToast({title:'加载中',icon:'loading'})
		//uni.startPullDownRefresh();
	},
	onShow(){
		this.getIndexData();
	},
	onReady(){
		//setTimeout(function () {
			//uni.hideLoading();
		//}, 500);
	},
	onPullDownRefresh() {
		this.getIndexData();
		uni.stopPullDownRefresh();
	},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	onNavigationBarButtonTap(e) {
		const openid = this.$store.state.openid;
		if(openid === null){
			uni.navigateTo({
				url: '/pages/user/login/login'
			})
			return false;
		}
	
		uni.navigateTo({
			url: '/pages/user/news/news'
		})
	},
	methods: {
		goRepay() {
			uni.switchTab({
				url: '/pages/tabBar/card/card'
			})
		},
		goRepayInfo(credit_id){
			uni.navigateTo({
				url: '/pages/card/credit/info?credit_id='+credit_id,
			})
		},
		getIndexData(){
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			try {
				this.$http.post(this.websiteUrl+'/api/api/getIndexData',{openid,sessionKey},(res) => {
					if(res.data.code==1){
						this.swiperList = res.data.result.banner;
						this.cardList = res.data.result.cardRepay;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false
				})
			}
		},
		goWebPage(title,url) {
			uni.navigateTo({
				url: '/pages/index/web-view/web-view?title='+title+'&url='+url
			})
		},
		goDetailPage(url) {
			const openid = this.$store.state.openid;
			if(openid === null){
				uni.navigateTo({
					url: '/pages/user/login/login'
				})
			}else{
				uni.navigateTo({
					url: url
				})
			}
			
		}
	}
}
